import React from 'react'
import styled from 'styled-components'
import GitHubCalendar from 'react-github-calendar';

const Hero = styled.section`
  display: flex;
  flex-direction:column;
  align-items: center;
  justify-content: center;
  height: calc(100vh - 60px);
  // background-image: url('https://source.unsplash.com/random');
  background-size: cover;
  background-position: center;
  text-align: center;
  color: #fff;
  background-color:#2a2f4c;

  h1 {
    font-size: 3rem;
    margin-bottom: 20px;
    color:rgb(205 205 255);
  }

  h3 {
    font-size: 1.5rem;
    margin-bottom: 40px;
    color:#cdcdff;
  }
`

const Container = styled.section`
  padding: 10px 0;
  background-color:#2a2f4c;

  h2 {
    font-size: 2.5rem;
    text-align: center;
    margin-bottom: 50px;
    color:rgb(205 205 255);
  }

  .profile-image {
    display: block;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin: 0 auto;
    border: 5px solid #333;
  }

  .bio {
    font-size: 1.5rem;
    line-height: 1.5;
    text-align: center;
    color:#cdcdff;
  }
`

const Home = () => {
  return (
    <div>
      <Hero>
          <h1>Hi, I am Saurabh Bhandari.</h1>
          <h3>A Full Stack Web Developer.</h3>
      </Hero>
      <Container>
          <h2>About Me</h2>
          <img src="https://avatars.githubusercontent.com/u/93419494?v=4" alt="" className="profile-image" />
          <p className="bio">
          A Full Stack Web Developer.
          </p>
        </Container>
        <GitHubCalendar style={{margin:"auto"}} username="SaurabhBH123" />     
    </div>
  )
}



export default Home