import React from 'react'
import styled from 'styled-components'

const Container = styled.section`
    padding: 100px 0;
    background-color:#2a2f4c;
  
    h2 {
      font-size: 2.5rem;
      text-align: center;
      margin-bottom: 50px;
      color:rgb(205 205 255);
    }
  
    .projects {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      margin: 0 -20px;
  
      .project {
        width: calc(33.3333% - 40px);
        margin: 20px;
        text-align: center;
        position: relative;
        overflow: hidden;
        border-radius: 4px;
  
        .project-image {
          width: 100%;
          height: 200px;
          object-fit: cover;
          transition: transform 0.3s;
        }
  
        .project-overlay {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(0, 0, 0, 0.5);
          display: flex;
          align-items: center;
          justify-content: center;
          transition: background-color 0.3s;
          color: #fff;
          font-size: 2rem;
          text-transform: uppercase;
          cursor: pointer;
  
          &:hover {
            background-color: rgba(0, 0, 0, 0.7);
            transform: scale(1.1);
          }
        }
      }
    }
  `

const Projects = () => {
  return (
    <div>
      <Container>
        <h2>My Projects</h2>
        <div className="projects">
          <div className="project">
            <img src="https://source.unsplash.com/random" alt="" className="project-image" />
            <div className="project-overlay">Project 1</div>
          </div>
          <div className="project">
            <img src="https://source.unsplash.com/random" alt="" className="project-image" />
            <div className="project-overlay">Project 2</div>
          </div>
          <div className="project">
            <img src="https://source.unsplash.com/random" alt="" className="project-image" />
            <div className="project-overlay">Project 3</div>
          </div>
        </div>
      </Container>
    </div>
  )
}

export default Projects