import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

const Container = styled.div`
  width: 100%;
  max-width: 1173px;
  margin: 0 auto;
  padding: 0 20px;
  background-color:#2a2f4c;
`

const Navbar = styled.nav`
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  background-color: #2a2f4c;
  color: #fff;

  .logo {
    font-size: 1.5rem;
    font-weight: bold;
  }

  ul {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;

    li {
      a {
        display: block;
        padding: 20px;
        color: #fff;
        text-decoration: none;
      }
    }
  }
`

