import React from 'react'
import styled from 'styled-components'


const Container = styled.section`
  padding: 100px 0;
  background-color:#2a2f4c;

  h2 {
    font-size: 2.5rem;
    text-align: center;
    margin-bottom: 50px;
    color:rgb(205 205 255);
  }

  .skills {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 -20px;

    .skill {
      width: calc(33.3333% - 40px);
      margin: 20px;
      text-align: center;

      h3 {
        font-size: 1.5rem;
        margin-bottom: 20px;
      }
  
      p {
        font-size: 1.2rem;
        color: #666;
        margin: 0;
        color:#cdcdff;
      }
    }
  `

const Skills = () => {
  return (
    <div>
      <Container>
          <h2>My Skills</h2>
          <div className="skills">
            <div className="skill">
              <h3>React.js</h3>
              {/* <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam euismod nulla eu ligula ornare, a dictum erat aliquet.</p> */}
            </div>
            <div className="skill">
              <h3>Node.js</h3>
              {/* <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam euismod nulla eu ligula ornare, a dictum erat aliquet.</p> */}
            </div>
            <div className="skill">
              <h3>JavaScript</h3>
              {/* <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam euismod nulla eu ligula ornare, a
              dictum erat aliquet.</p> */}
          </div>
          <div className="skill">
            <h3>HTML</h3>
            {/* <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam euismod nulla eu ligula ornare, a dictum erat aliquet.</p> */}
          </div>
          <div className="skill">
            <h3>CSS</h3>
            {/* <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam euismod nulla eu ligula ornare, a dictum erat aliquet.</p> */}
          </div>
          <div className="skill">
            <h3>Git</h3>
            {/* <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam euismod nulla eu ligula ornare, a dictum erat aliquet.</p> */}
          </div>
        </div>
      </Container>
    </div>
  )
}

export default Skills